import { LocalStorage, FilteredStorage } from "../js/utils";
import useDispatchAction from "./useDispatchAction";
import useMessage from "./useMessage";

export interface Favorites extends FilteredStorage {
    placeAlreadyStored: Function;
    placeNotYetStored: Function;
    showSize: Function;
    manageSupport: Function;
    add: Function;
    remove: Function;
    areEmpty: Function;
    containsBooks: Function;
}
const useFavorites = () => {
    const { cacheSupported } = useDispatchAction();
    const showMessage = useMessage();
    const favorites = new FilteredStorage(item => item.kind === "books#volume") as Favorites;

    const showFavoritesSize = () => {
        const length = favorites.getLength();
        let storageReport = "";
        if (length) {
            storageReport = `Funkcjonalno lokalnego magazynu danych jest aktywna. W Ulubionych jest ${length}  ksiek`;
        } else {
            storageReport = `Funkcjonalno lokalnego magazynu danych jest aktywna. W Ulubionych nie ma ksiek`;
        }
        showMessage.info(storageReport);
    };

    const manageSupport = () => {
        const isSupported = LocalStorage.isSupported();
        if (isSupported) {
            cacheSupported();
        } else {
            showMessage.warning("Ulubione nie s obsugiwane");
        }

        return isSupported;
    };

    const addToFavorites = (label: string, item: Object) => {
        try {
            LocalStorage.set(label, item);

            return true;
        } catch (error) {
            return false;
        }
    };

    const getAllItems = () => {
        const result = favorites.getAllItems();

        return result;
    };
    const remove = (id: string) => {
        LocalStorage.remove(id);
    };
    const areEmpty = () => {
        return favorites.getLength() === 0 ? true : false;
    };
    const containsBooks = () => {
        return favorites.getLength() === 0 ? false : true;
    };
    favorites.showSize = showFavoritesSize;
    favorites.manageSupport = manageSupport;
    favorites.add = addToFavorites;
    favorites.getAllItems = getAllItems;
    favorites.remove = remove;
    favorites.areEmpty = areEmpty;
    favorites.containsBooks = containsBooks;

    return { favorites };
};
export default useFavorites;
