import { ThunkAction } from "redux-thunk";
import { AnyAction } from "redux";

import { googleAPI } from "config";
import { showError } from "../actionCreators";
import { RootStateType, Favorites } from "types";
import { NavigateFunction } from "react-router-dom";
import Paths from "Routing/Paths";

export interface ThunkAddBookToFavoritesArgs {
    id: string;
    favorites: Favorites;
    navigate: NavigateFunction;
    showMessage: any;
}

export const thunkAddBookToFavorites = ({ id, favorites, navigate, showMessage }: ThunkAddBookToFavoritesArgs): ThunkAction<void, RootStateType, unknown, AnyAction> => {
    return async (dispatch, getState) => {
        const path = googleAPI + id;

        fetch(path)
            .then(res => res.json())
            .then(json => {
                if (json) {
                    try {
                        favorites.add(id, json);
                        showMessage.success("Poprawnie dodano do ulubionych nastpujc ksik: " + json.volumeInfo.title);
                    } catch (error) {
                        dispatch(
                            showError({
                                isError: true,
                                errorMessage: "An attempt to add item to local storage caused error",
                            })
                        );
                        navigate(Paths.error);
                    }
                } else {
                    navigate(Paths.not_found);
                }
            })
            .catch(error => {
                const result = {
                    isError: true,
                    errorMessage: error.message,
                };
                dispatch(showError(result));
                navigate(Paths.error);
            });
    };
};
