import { AnyAction } from "redux";
import { ThunkAction } from "redux-thunk";
import { NavigateFunction } from "react-router-dom";

import { googleAPI } from "config";
import { fetchDetails } from "../actionCreators";
import { RootStateType } from "types";
import Paths from "Routing/Paths";

export interface ThunkFetchBookDetailsArgs {
    navigate: NavigateFunction;
    id: string;
}
const thunkFetchBookDetails = ({ navigate, id }: ThunkFetchBookDetailsArgs): ThunkAction<void, RootStateType, unknown, AnyAction> => {
    return async dispatch => {
        const path = googleAPI + id;
        dispatch(fetchDetails(path));
        navigate(Paths.details);
    };
};

export default thunkFetchBookDetails;
