import Paths from "Routing/Paths";
import { store } from "components/AppProvider";
import { FormatFetchedBooks, getValue } from "js/utils";
import { useNavigate } from "react-router-dom";
import useMessage from "./useMessage";

type FoundBooks = {}[];
interface FetchSummary {
    isError?: boolean;
    data?: FoundBooks;
    errorMessage?: string;
}

const STEP = 40;
const INITIAL_FETCH_SUMMARY: FetchSummary = { isError: false };
const INITIAL_FOUND_BOOKS: FoundBooks = [];

export const useFetchBooks = () => {
    const navigate = useNavigate();
    const showMessage = useMessage();
    const fetchBooksFromAPI = (path: string) => {
        let startIndex = 0;
        let foundBooks = INITIAL_FOUND_BOOKS;
        let fetchSummary = INITIAL_FETCH_SUMMARY;

        async function recursiveFetch() {
            const fullPath = path + startIndex.toString();
            const fetchResult = await fetch(fullPath).catch(error => {
                fetchSummary.isError = true;
                fetchSummary.errorMessage = error.message || "Unknown error";
                store.dispatch({ type: "ERROR_SHOW", payload: fetchSummary });
                console.log("fetch result error");
                navigate(Paths.error);
            });
            if (fetchResult) {
                const response = await fetchResult.json();
                const code = getValue(response, "code");
                if (code >= 400 && code <= 599) {
                    const message = getValue(response, "message");
                    fetchSummary.isError = true;
                    fetchSummary.errorMessage = message || "Unnamed error";
                    store.dispatch({ type: "ERROR_SHOW", payload: fetchSummary });
                    navigate(Paths.error);
                    console.log("cod 400 or 500");
                    return;
                }

                if (response.items) {
                    startIndex += STEP;
                    foundBooks = foundBooks.concat(response.items);
                    console.log("response items");
                    recursiveFetch();
                } else {
                    if (response.error) {
                        store.dispatch({ type: "ERROR_SHOW", payload: fetchSummary });
                        navigate(Paths.error);
                        console.log("response error");
                    }

                    if (!response.eror && foundBooks.length === 0) navigate(Paths.not_found);
                    else {
                        fetchSummary.data = FormatFetchedBooks.Run(foundBooks);
                        store.dispatch({ type: "BOOKS_FETCH", payload: fetchSummary });
                        store.dispatch({ type: "IS_FETCHED_FROM_URL_SET", payload: true });
                        showMessage.success(`Poprawnie pobrano dane, cznie pobrano ${fetchSummary.data.length.toString()} ksiek`);
                        navigate(Paths.books);
                    }
                }
            }
        }
        recursiveFetch();
    };
    return fetchBooksFromAPI;
};

export default useFetchBooks;
