import { connect } from "react-redux";

import { useCreateDebouncedCallback, useFavorites, useMessage } from "hooks";
import { thunkAddBookToFavorites, ThunkAddBookToFavoritesArgs } from "js/redux/thunks/thunkAddBookToFavorites";

import RoundIconButton from "./RoundIconButton";
import { AppDispatch } from "types";
import { useNavigate } from "react-router-dom";

interface Props {
    bookID: string;
    thunkAddBookToFavorites: (arg0: ThunkAddBookToFavoritesArgs) => void;
}

export const AddBookToFavoritesButton = (props: Props) => {
    const { bookID, thunkAddBookToFavorites } = props;
    const { favorites } = useFavorites();
    const navigate = useNavigate();
    const showMessage = useMessage();

    const AddBookToFavorites = useCreateDebouncedCallback(thunkAddBookToFavorites, {
        id: bookID,
        favorites: favorites,
        navigate: navigate,
        showMessage: showMessage,
    });

    return <RoundIconButton type="addToFavorites" ID={bookID} clickHandler={AddBookToFavorites} />;
};

const mapDispatchToProps = (dispatch: AppDispatch) => ({
    thunkAddBookToFavorites: ({ id, favorites, navigate, showMessage }: ThunkAddBookToFavoritesArgs) => dispatch(thunkAddBookToFavorites({ id, favorites, navigate, showMessage })),
});

export default connect(null, mapDispatchToProps)(AddBookToFavoritesButton);
