import { connect } from "react-redux";

import thunkFetchBookDetails, { ThunkFetchBookDetailsArgs } from "js/redux/thunks/thunkFetchBookDetails";
import RoundIconButton from "./RoundIconButton";

import { useCreateDebouncedCallback } from "hooks";
import { AppDispatch } from "types";
import { useNavigate } from "react-router-dom";

interface Props {
    bookID: string;
    ThunkFetchBookDetails: (arg0: ThunkFetchBookDetailsArgs) => void;
}
export const ShowBookDetailsButton = (props: Props) => {
    const { bookID, ThunkFetchBookDetails } = props;
    const navigate = useNavigate();

    const fetchBookDetails = useCreateDebouncedCallback(ThunkFetchBookDetails, {
        navigate: navigate,
        id: bookID,
    });

    return <RoundIconButton type="showFullInfo" ID={bookID} clickHandler={fetchBookDetails} />;
};

const mapDispatchToProps = (dispatch: AppDispatch) => ({
    ThunkFetchBookDetails: ({ navigate, id }: ThunkFetchBookDetailsArgs) => {
        dispatch(thunkFetchBookDetails({ navigate, id }));
    },
});

export default connect(null, mapDispatchToProps)(ShowBookDetailsButton);
