import debounce from "lodash/debounce";
import * as React from "react";

import { useDispatchAction, useFavorites } from "hooks";

import RoundIconButton from "./RoundIconButton";

interface Props {
    bookID: string;
}

const RemoveBookFromFavoritesButton = (props: Props) => {
    const { bookID } = props;
    const { removeBook: remove } = useDispatchAction(); // todo przerobi
    const { favorites } = useFavorites();
    //eslint-disable-next-line react-hooks/exhaustive-deps
    const RemoveFromFavorites = React.useCallback(
        debounce(() => {
            if (bookID) {
                favorites.remove(bookID);
                remove({ id: bookID }); //todo tu by przerobi ID na book, ale to by trza a do akcji
            }
        }, 200),
        [remove, bookID]
    );

    return <RoundIconButton type="removeBookFromFavorites" ID={bookID} clickHandler={RemoveFromFavorites} />;
};
export default RemoveBookFromFavoritesButton;
